.ramdom_tweet {
  margin-top: 10px;
  border-bottom: 1px solid #80808045;
}

.top_section {
  display: flex;
  justify-content: center;
  align-items: center;
}

.image {
  width: 60px;
  border-radius: 50%;
  margin-left: 20px;
  margin-top: 20px;
}

.name_user {
  display: flex;
}

.name {
  display: flex;
  font-weight: 550;
}

.username {
  font-weight: 400;
  font-size: 14px;
  color: gray;
  margin-left: -5px;
}

.placeholder {
  color: gray;
  padding-left: 10px;
  margin-top: 10px;
}

.bottom_section {
  display: flex;
  justify-content: space-evenly;
}

.icons {
  display: flex;
  justify-content: space-evenly;
  margin-top: 20px;
  margin-bottom: 10px;
}
