.mainTweetIcons {
  display: flex;
  border-bottom: 1px solid #80808045;
  padding-bottom: 10px;
}

h1 {
  font-weight: 200;
  font-family: Helvetica;
  margin-left: 10px;
  margin-top: 10px;
}

.imageMain {
  height: 60px;
  width: 60px;
  border-radius: 50%;
  margin-left: 20px;
}

.profile_placeholder {
  display: flex;
  align-items: center;
}

.input {
  color: gray;
  margin-left: 20px;
  padding: 10px 50% 10% 10px;
  max-width: 100%;
  border: none;
}

.input::placeholder {
  font-family: Helvetica;
  font-size: 1.1em;
}

.icons {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  margin-left: 32px;
}

.icon {
  margin-left: 30px;
}

.tweet {
  margin-left: 50%;
}

button {
  border-radius: 20px;
  padding: 10px 30px;
  background-color: rgb(29, 155, 240);
  border: none;
}

button:hover {
  background-color: rgb(1, 128, 212);
}

@media screen and (min-width: 768px) {
  .tweet {
    margin-left: 25%;
  }
}

@media screen and (min-width: 1024px) {
  .tweet {
    margin-left: 40%;
  }
}

@media screen and (min-width: 1280px) {
  .tweet {
    margin-left: 50%;
  }
}
