.leftComponent {
  /* height: 100vh; */
  position: sticky;
  width: 30vw;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  padding-left: 3%;
  margin-top: 3%;
}

.icon_section {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: row;
  margin-bottom: 10%;
  padding: 10% 10%;
}

.icon_section:hover {
  background-color: rgb(204, 203, 203);
  border-radius: 20px;
}

span {
  margin-left: 1rem;
  margin-right: 1rem;
  font-size: 18px;
  font-weight: 500;
  font-family: Helvetica;
}

.btn {
  display: flex;
  justify-content: space-around;
}
